import React from 'react'
import DatePicker from 'react-datepicker'
import moment from 'moment'
import 'react-datepicker/dist/react-datepicker.css'
import api from './api'
import './datepicker.scss'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      startDate: moment(),
      datesWithUsers: []
    }
    this.handleChange = this.handleChange.bind(this)
  }

  componentDidMount() {
    api.fetchUsers()
    .then(res => {
      res.map(user => moment.unix(user.attributes.created).format('YYYYMMDD'))
      .map(date => {
        return !this.state.datesWithUsers.includes(date) && this.setState({
          datesWithUsers: [
            ...this.state.datesWithUsers,
            date
          ]
        })
      })
    })
  }

  handleChange(date) {
    this.setState({
      startDate: date
    })
  }

  _getDatesWithUsers(day) {
    const res = this.state.datesWithUsers.includes(moment(day).format('YYYYMMDD')) ?
      'with-users' : undefined
    return res
  }

  render() {
    return (
      <DatePicker
        inline
        selected={this.state.startDate}
        onChange={this.handleChange}
        dayClassName={day => this._getDatesWithUsers(day)}
      />
    )
  }
}

export default App


